import { style } from '../wxs/utils';
import { addUnit } from '../wxs/add-unit';

function rootStyle(data) {
  return style({
    'z-index': data.zIndex,
    top: addUnit(data.top)
  });
}

function notifyStyle(data) {
  return style({
    background: data.background,
    color: data.color
  });
}

export { rootStyle, notifyStyle };